<style scoped>
    .pages {
        text-align: left;
        padding: 0 20px;
    }

    .form {
        width: 100%;
        margin-top: 30px;
    }
</style>

<template>
    <div class="pages">
        <H3>权限管理</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" v-model="form" label-width="120px">
            <el-form-item label="权限名称" prop="name">
                <el-input style="width: 500px" v-model="form.name" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="权限简介" prop="name">
                <el-input style="width: 500px" v-model="form.description" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="权限路径" prop="path">
                <el-input style="width: 500px" v-model="form.path" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="状态" prop="name">
                <el-select v-model="form.status" placeholder="请选择">
                    <el-option
                            label="启用"
                            :value="1">
                    </el-option>
                    <el-option
                            label="不启用"
                            :value="0">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="权限等级" prop="name">
                <el-select v-model="form.level" placeholder="请选择">
                    <el-option
                            label="一级"
                            :value="1">
                    </el-option>
                    <el-option
                            label="二级"
                            :value="2">
                    </el-option>
                    <el-option
                            label="三级"
                            :value="3">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="权限排序" prop="name">
                <el-input style="width: 500px" v-model="form.sort" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="父节点" prop="name">
                <el-input style="width: 500px" v-model="form.pid" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="图标" prop="name">
                <el-input style="width: 200px" v-model="form.icon" placeholder="请输入">
                </el-input>
                <i style="margin-left: 30px" :class="'icon iconfont '+form.icon "></i>
            </el-form-item>

            <el-form-item style="margin-top: 50px">
                <el-button type="primary" style="margin-left: 50px" :loading="isLoading"
                           @click="onSubmitInfo">提交
                </el-button>
                <el-button plain style="margin-left: 50px"
                           @click="$router.back()">返回
                </el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import api from '../../js/api/permission'

    export default {
        name: "permissionDetail",
        data() {
            return {
                form: {},
                isNoEdit: true,
                isLoading: false,
                data: {},
            }
        },
        mounted() {
            if (this.$route.query.id !== undefined) {
                this.isNoEdit = true;
                this.loadData();
            } else {
                this.isNoEdit = false;
            }
        },
        methods: {
            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                    this.data = api.deepCopy(this.form);
                })
            },
            edit() {
                this.isNoEdit = false;
            },
            cancelInfo() {
                this.form = this.data;
                this.isNoEdit = true;
            },
            onSubmitInfo() {
                this.isLoading = true;
                api.add(this.form, () => {
                    this.isLoading = false;
                    this.$message.success('保存成功');
                    this.$router.push({name: 'permission'});
                    this.$route.meta.keepAlive = false;
                }, () => {
                    this.isLoading = false;
                })
            }
        }
    }
</script>
